@import './common/style/sass/mixins';

.appContainer {
  height: 100vh;
  background-image: url("./assets/img/backGroundImage.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  .letterContainer{
    height: 100%;
    @include flex(column, center, center, wrap);

    .letter{
      width: 800px;
      height: 500px;
      border: 1px solid #4b4b4b;
      background-color: rgba(255,255,255,0.7);
      border-radius: 10px;
      @include flex(column, center, center, wrap);


      .lang {
        @include flex(column, flex-start, flex-start, wrap);
        @include text(24,3,600);

        .eng, .rus {
          margin-left: 70px;
          font-weight: 500;
        }
      }

      .behavior{
        height: 100%;
        @include flex(column, center, center, wrap);


      }

      .form {
        height: 90%;
        width: 90%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .underTree {
          @include flex(column, center, center, wrap);

        }
      }
    }
  }
}



.input {
  width: 300px;
  height: 20px;
  margin: 10px 0;
  padding: 5px 10px;
  border: 0px solid black;
  border-radius: 10px;
}

.textArea {
  width: 300px;
  height: 130px;
  margin: 10px 0;
  padding: 5px 10px;
  border: 0px solid black;
  border-radius: 10px;
  resize: none;
}

.button {
  width: 320px;
  height: 30px;
  border: 0px solid black;
  border-radius: 10px;
  cursor: pointer;
}